header {
    text-align: center;
    padding: 10px;
    box-shadow: rgba(204, 204, 204, 0.329) 0px 2px 10px;
    height: 50px;
}
  
header a {
    color: #000;
    font-weight: 500;
    transition: all .2s linear;
    border-bottom: 2px solid transparent;
    font-size: 18px;
    
    display: inline-block;
    margin: 0 10px;
}
  
header a:hover {
    color: #d15700
}
  